.wrapper {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
  direction: rtl;
  flex-wrap: wrap;
}
.calendar {
  margin: 5px;
}
.primary {
  background-color: #0074d9;
  border: 1px solid white;
  color: white;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  font-family: sans-serif;
}
.rmdp-wrapper {
  display: flex;
  flex-direction: column;
}
